@import "./__mixin";

%__fontDefault {
    font-size: 1em; }
%__font16 {
    font-size: 1.6em; }
%__font18 {
    font-size: 1.8em; }
%__font30 {
    font-size: 3em; }
%__font28 {
    font-size: 2.8em !important; }
%__font26 {
    font-size: 2.6em; }
%__font25 {
    font-size: 2.5em; }
%__font22 {
    font-size: 2.2em; }
%__font15 {
    font-size: 1.5rem;
    @include m() {
        font-size: 1.3rem; } }
%__font14 {
    font-size: 1.4rem; }
%__fontOswald {
    font-family: 'Oswald', sans-serif; }
